import {connect} from "react-redux";

import {
    followThunkCreator,
    getUsersThunkCreator,
    setTotalUsersCountAction,
    setUsersAction,
    setUsersPageAction,
    unfollowThunkCreator
} from "../redux/users_reducer";
import React from "react";
import UsersApi from "../api_components/UsersApi";
import {WithAuthRedirect} from "../hoc/WithAuthRedirect";
import {compose} from "redux";
import {
    getIsAuth,
    getisButtonProgressing,
    getIsFetching,
    getTotalUsersCount,
    getUsersCurrentPage,
    getUsersData,
    getUsersPageSize
} from "../selectors/users_selectors";


const mapStateToProps = (state) => {

    return {
        usersData: getUsersData(state),
        usersCurrentPage: getUsersCurrentPage(state),
        totalUsersCount: getTotalUsersCount(state),
        usersPageSize: getUsersPageSize(state),
        isFetching: getIsFetching(state),
        isButtonProgressing: getisButtonProgressing(state),
        isAuth: getIsAuth(state)

    }
}


const UsersContainer = compose(connect(mapStateToProps, {

        setUsers: setUsersAction,
        setUsersPage: setUsersPageAction,
        setTotalUsersCount: setTotalUsersCountAction,
        getUsersThunkCreator,
        unfollowThunkCreator,
        followThunkCreator
    }),
    WithAuthRedirect
)(UsersApi)


export default UsersContainer