import getApi from "../DAL_api/api";

const FOLLOW_TODO = 'todos/FOLLOW_TODO'
const UNFOLLOW_TODO = 'todos/UNFOLLOW_TODO'
const SET_TODOS = 'todos/SET_TODOS'
const SET_CURRENT_PAGE = 'todos/SET_CURRENT_PAGE'
const TOTAL_TODOS_COUNT = 'todos/TOTAL_TODOS_COUNT'
const IS_LOADING = 'todos/IS_LOADING'
const IS_TODO_BUTTON_PROGRESSING = 'todos/IS_TODO_BUTTON_PROGRESSING'

const initialState = {
    todosData: [],
    totalCount: 40,
    pageSize: 10,
    currentPage: 1,
    isFetching: true,
    isTodoButtonProgressing: []
}


export const todoReducer = (state = initialState, action) => {

    switch (action.type) {
        case FOLLOW_TODO:

            return {
                ...state,
                todosData: state.todosData.map(todo => {
                    if (todo.id === action.todoId) {
                        return {...todo, completed: false}
                    }
                    return todo
                })
            }

        case UNFOLLOW_TODO:
            return {
                ...state,
                todosData: state.todosData.map(todo => {
                    if (todo.id === action.todoId) {
                        return {...todo, completed: true}
                    }
                    return todo
                })
            }
        case SET_TODOS:
            return {...state, todosData: action.todos}

        case SET_CURRENT_PAGE:
            return {...state, currentPage: action.currentPage}

        case TOTAL_TODOS_COUNT:
            return {...state, totalCount: action.count}

        case IS_LOADING:
            return {...state, isFetching: action.loading}

        case IS_TODO_BUTTON_PROGRESSING:
            return {
                ...state,

                isTodoButtonProgressing: action.todoProgressing
                    ? [...state.isTodoButtonProgressing, action.todoId]
                    : state.isTodoButtonProgressing.filter(id => id !== action.todoId)
            }

        default:
            return state
    }
}


export const followTodoAction = (todoId) => ({type: FOLLOW_TODO, todoId})
export const unFollowTodoAction = (todoId) => ({type: UNFOLLOW_TODO, todoId})
export const setTodosAction = (todos) => ({type: SET_TODOS, todos})
export const setPageAction = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setTotalCountAction = (count) => ({type: TOTAL_TODOS_COUNT, count})
export const isTodoLoadingAction = (loading) => ({type: SET_CURRENT_PAGE, loading})
export const isToggleButtonAction = (todoProgressing, todoId) => ({
    type: IS_TODO_BUTTON_PROGRESSING,
    todoProgressing,
    todoId
})

export const getTodosThunkCreator = (currentPage, totalCount) => async (dispatch) => {
    dispatch(isTodoLoadingAction(true))
    //axios
    let data = await getApi.getTodos(currentPage, totalCount)
        dispatch(isTodoLoadingAction(false))
        dispatch(setPageAction())
        dispatch(setTodosAction(data))
        dispatch(setTotalCountAction(data.userId))
}

export const getTodosFollowCreator = (todoId) => async (dispatch) => {
        dispatch(isToggleButtonAction(true, todoId))

        let res = await getApi.getFollowTodo(todoId)
            if (res.data.id) {
                dispatch(followTodoAction(todoId))
            }
        dispatch(isToggleButtonAction(false, todoId))
    }


export const getTodosUnfollowCreator = (todoId) => async (dispatch) => {
        dispatch(isToggleButtonAction(true, todoId))

        let res = await getApi.getUnfollowTodo(todoId)
            if (res.data.id) {
                dispatch(unFollowTodoAction(todoId))
            }
        dispatch(isToggleButtonAction(false, todoId))
    }