import React from 'react';
import {Box, Button, Checkbox, FormControlLabel, Grid, TextField} from "@material-ui/core";
import {Field, reduxForm} from "redux-form";
import {email, minLength6, required} from "../../utills/forms-validators";

const  LoginForm = (props) => {

    const  renderTextField = ({
                        label,
                        input,
                        meta: {touched, warning, error},
                        ...custom
                    }) => {
        return(
            <TextField
                label={label}
                error={touched && error && <span>error</span>}
                helperText={touched && error}
                {...input}
                {...custom}
            />
        )
    }

    const renderCheckbox = ({ input, label }) => {
        return (
            <div>
                <FormControlLabel
                    control={
                        <Checkbox
                            color={'primary'}
                            checked={input.value ? true : false}
                            onChange={input.onChange}
                        />
                    }
                    label={label}
                />
            </div>
        )
    }

    return (
        <div>
            <Grid container>
                <Grid item xs={12}>
                    <form onSubmit={props.handleSubmit}>
                        <Box mb={2}>
                            <Field
                                autoComplete="on"
                                fullWidth={true}
                                id="outlined-email-input"
                                variant="outlined"
                                name="email"
                                label="Email"
                                type="email"
                                validate={[required, email]}
                                component={renderTextField}/>
                        </Box>

                        <Box mb={2}>
                            <Field
                                component={renderTextField}
                                name="password"
                                fullWidth={true}
                                id="outlined-password-input"
                                label="Password"
                                type="password"
                                autoComplete="on"
                                variant="outlined"
                                validate={[required, minLength6]}
                            />
                        </Box>

                        <Field name={'rememberMe'} component={renderCheckbox}  label={'Remember me'} />

                        <Box mb={2}>
                            <Button  type={"submit"} variant={"contained"} color={"primary"} fullWidth={true}>
                                Login
                            </Button>
                        </Box>

                    </form>
                </Grid>
            </Grid>
        </div>
    )
}

const ContactLoginForm = reduxForm({
    form: 'login',
})(LoginForm)

export default ContactLoginForm;