import getApi from "../DAL_api/api";
import {stopSubmit} from "redux-form";

const SET_AUTH_DATA = 'auth/IS_AUTH'
const SET_LOGIN_DATA = 'auth/SET_LOGIN_DATA'

let initialState = {
    id: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_DATA:

            return {
                ...state,
                ...action.data,
                isAuth: true
            }
        case SET_LOGIN_DATA:
            return {
                ...state,
                ...action.data,
                isAuth: true
            }
        default:
            return state
    }
}

export const authReducerAction = (data) => ({type: SET_AUTH_DATA, data})
export const loginReducerAction = (id, email, login, isAuth) => ({
    type: SET_LOGIN_DATA,
    data: {id, email, login, isAuth}
})
export default authReducer


export const getThunkAuth = () => async (dispatch) => {

    let res = await getApi.getNav()
        if (res.data.resultCode === 0) {
            dispatch(authReducerAction(res.data.data))
        }

}

export const getThunkLogin = (email, password, rememberMe) => async (dispatch) => {
        let res = await getApi.login(email, password, rememberMe)

                if (res.resultCode === 0) {
                    dispatch(loginReducerAction())
                } else {

                    let error = res.data.messages.length > 0 ? res.data.messages[0] : 'Some error'
                    dispatch(stopSubmit('login', {
                        email: error,
                        password: error
                    }))
                }
    }

export const getThunkLogout = () => async (dispatch) => {
        let res = await getApi.logout()
                if (res.resultCode === 0) {
                    dispatch(loginReducerAction(null, null, null, false))
                }
    }