import React from "react";
import {connect} from "react-redux";
import {
    addDialogsActionCreater,
    getProfileThunk,
    getStatusProfileThunk,
    updateStatusprofileThunk
} from "../redux/profile_reducer";
import ProfileApi from "../api_components/ProfileApi";
import {withRouter} from "react-router";
import {compose} from "redux";


const mapStateToProps = (state) => {
    return {
        profile: state.dialogsData.profile,
        dialogs: state.dialogsData.dialogs,
        newDialogsText: state.dialogsData.newDialogsText,
        isAuth: state.authData.isAuth,
        status: state.dialogsData.status,
    }
}



const ProfileContainer = compose(
    connect(mapStateToProps, {
        addDialogs: addDialogsActionCreater,
        getProfileThunk,
        getStatusProfileThunk,
        updateStatusprofileThunk
    }),
    withRouter
) (ProfileApi)

export default ProfileContainer