import React, {Component, lazy, Suspense} from 'react';
// import Todos from "../components/Todos/Todos";
import {ProgressLoader} from "../components/Loader/ProgressLoader";
const Todos = lazy(() => import('../components/Todos/Todos'))

class TodosApi extends Component {
    constructor(props) {

        super(props);
        this.onChangePage = this.onChangePage.bind(this)
        this.followTodos = this.followTodos.bind(this)
        this.unfollowTodos = this.unfollowTodos.bind(this)
    }

    followTodos(id) {
        this.props.getTodosFollowCreator(id)
    }

    unfollowTodos(id) {
        this.props.getTodosUnfollowCreator(id)
    }

    componentDidMount() {
        this.props.getTodosThunkCreator(this.props.currentPage, this.props.totalCount)
    }

    onChangePage(pageNumber) {
        this.props.getTodosThunkCreator(pageNumber, this.props.totalCount)
    }


    render() {
        return (
            <div>
                {
                    this.props.isFetching
                        ? <ProgressLoader/>
                        : <Suspense fallback={<strong>Loading...</strong>}>
                            <Todos
                                followTodos={this.followTodos}
                                unfollowTodos={this.unfollowTodos}
                                {...this.props}
                                onChangePage={this.onChangePage}
                            />
                        </Suspense>
                }
            </div>
        );
    }
}


export default TodosApi;