import React from "react";
import './nav.css'
import {NavLink} from "react-router-dom";
import {AppBar, Button, Toolbar, Typography} from "@material-ui/core";
import useStyles from "../../styles/navStyles";
import SideBar from "../SideBar/SideBar";
import LogoutReduxForm, {LogoutSelect} from "../Select/LogoutSelect";


const Nav = (props) => {

    const classes = useStyles()

        const authLogout = () => {
            props.getThunkLogout()
        }
    return (
        <AppBar position="static">
            <Toolbar>
                <SideBar {...props}/>

                <div className={classes.navRoot}>
                    <Typography variant="h6" className={classes.title}>
                        <NavLink className="navLink" activeClassName="active" to="/">
                            Home
                        </NavLink>
                        <NavLink className="navLink" activeClassName="active"
                                 to="/users">Users
                        </NavLink>
                        <NavLink className="navLink" activeClassName="active"
                                 to="/posts">Posts</NavLink>
                    </Typography>

                    {props.isAuth ? <LogoutReduxForm onSubmit={authLogout} {...props}/>
                    : <div>
                            <Button color="inherit" >
                                <NavLink className="navLink" to="/login">
                                    Login
                                </NavLink>
                            </Button>
                            <Button color="inherit">
                                <NavLink className="navLink" to="/register">
                                    Register
                                </NavLink>
                            </Button>
                        </div>}
                </div>
            </Toolbar>
        </AppBar>
    )
}

export default Nav