import getApi from "../DAL_api/api";

const ADD_DIALOG_MESSAGE = 'ADD_DIALOG_MESSAGE'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS_PROFILE = 'SET_STATUS_PROFILE'
const UPDATE_STATUS_PROFILE = 'UPDATE_STATUS_PROFILE'
const DELETE_MESSAGE = 'DELETE_MESSAGE'


let initialState = {
    dialogs: [
        {
            id: 1,
            message: 'Hello Man!',
            likes: 10,
            img: 'https://images.unsplash.com/photo-1508919801845-fc2ae1bc2a28?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8aW1nfGVufDB8fDB8fA%3D%3D&ixlib=rb-1.2.1&w=1000&q=80'
        },

    ],


    profile: null,
    status: '',
}

export const dialogReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_DIALOG_MESSAGE:

            let newDialog = action.newMessage
            return {
                ...state,
                dialogs: [...state.dialogs, {
                    id: 4,
                    message: newDialog,
                    likes: 44,
                    img: 'https://www.paralympic.org.au/wp-content/uploads/2020/01/Luke-Missen-Para-badminton-Photo-scaled-e1586230999716.jpg'
                }],
            }

        case SET_USER_PROFILE:
            return {...state, profile: action.profile}

        case SET_STATUS_PROFILE:
            return {...state, status: action.status}
        case UPDATE_STATUS_PROFILE:
            return {...state, status: action.status}
        case DELETE_MESSAGE:
            return {
                ...state, dialogs: state.dialogs.filter(d => d !== action.id)
            }
        default :
            return state
    }

    return state
}

export const addDialogsActionCreater = (newMessage) => ({type: ADD_DIALOG_MESSAGE, newMessage})
export const setUserProfileActionCreater = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatusProfileActionCreater = (status) => ({type: SET_STATUS_PROFILE, status})
export const updateStatusProfileActionCreater = (status) => ({type: UPDATE_STATUS_PROFILE, status})
export const deleteMessageActionCreater = (id) => ({type: DELETE_MESSAGE, id})

export const getProfileThunk = (userId) => async (dispatch) => {

    let data = await getApi.getProfile(userId)
    dispatch(setUserProfileActionCreater(data))
}

export const getStatusProfileThunk = (userId) => async (dispatch) => {
    let res = await getApi.getStatus(userId)
    dispatch(setStatusProfileActionCreater(res.data))
}

export const updateStatusprofileThunk = (status) => async (dispatch) => {
    let res = await getApi.updateStatus(status)
    if (res.data.resultCode === 0) {
        dispatch(updateStatusProfileActionCreater(status))
    }
}