const ADD_POST = 'posts/ADD_POST'

let initialState = {
        posts: [
            {id: 1, message: 'this is post-1', likes: 11},
            {id: 2, message: 'this is post-2', likes: 12},
        ],
    }

 export const postReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST:

            let newText =  action.newPost

            return {
                ...state,
                posts: [...state.posts, {id: 3, message: newText}],
                
            }

        default:
            return {...state}

    }


    return state
}
export const addPostActionCreater = (newPost) => ({type: ADD_POST, newPost})
