import React from "react";
import {Box, Container, Typography} from "@material-ui/core";
import ContactLoginForm from "../components/Login/LoginForm";
import {connect} from "react-redux";
import {getThunkLogin} from "../redux/auth_reducer";
import {Redirect} from "react-router";


const Login = (props) => {

    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={'/users'} />
    }

    return (
        <div>
            <Container maxWidth={"xs"}>
                <Box m={3}>
                    <Typography  variant={'h3'}>Login</Typography>
                    <ContactLoginForm onSubmit={onSubmit} />
                </Box>
            </Container>

        </div>
    )
}

const mapStateToProps = state => {

    return {
        isAuth: state.authData.isAuth
    }
}

const LoginReduxConnect = connect(mapStateToProps, {
    login: getThunkLogin,
})(Login)

export default LoginReduxConnect

