import React, {Component, lazy, Suspense} from "react";
import {ProgressLoader} from "../components/Loader/ProgressLoader";
const Profile = lazy(() => import('../pages/Profile'))

class ProfileApi extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        let userId = this.props.match.params.id
        if (!userId) {
            userId = 9
        }
        this.props.getProfileThunk(userId)
        this.props.getStatusProfileThunk(userId)
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return nextProps !== this.props
    }


    render() {
        return (
            <Suspense fallback={<ProgressLoader />}>
                <Profile {...this.props} updateStatusprofileThunk={this.props.updateStatusprofileThunk}/>
            </Suspense>
        )
    }
}

export default ProfileApi