import getApi from "../DAL_api/api";

const IS_APP_LOADING = 'app/IS_APP_LOADING'


let initialState = {
    isAppLoading: false,
    fake: 11
}

export const  appReducer = (state = initialState, action) => {
    switch (action.type) {
        case IS_APP_LOADING:
            return {
                ...state,
                isAppLoading: true
            }
        case 'FAKE':
            return {...state, fake: state.fake}
        default:
            return state
    }
}

export const appLoadingCreator = () => ({type: IS_APP_LOADING})


export const appLoadingThunk = () => async(dispatch) => {
    let res = await getApi.getNav()
    dispatch(appLoadingCreator(res.data))
}
