import React, {lazy, Suspense} from "react";
import {ProgressLoader} from "../components/Loader/ProgressLoader";

const UsersCard = lazy(() => import('../components/Users/UsersCard'))

class UsersApi extends React.Component {
    constructor(props) {

        super(props);
        this.onChangePage = this.onChangePage.bind(this)
        this.followApi = this.followApi.bind(this)
        this.unfollowApi = this.unfollowApi.bind(this)
    }

    followApi(id) {
        this.props.followThunkCreator(id)
    }

    unfollowApi(id) {
        this.props.unfollowThunkCreator(id)
    }

    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.usersCurrentPage, this.props.totalUsersCount)
    }

    onChangePage(pageNumber) {
        this.props.getUsersThunkCreator(pageNumber, this.props.totalUsersCount)
    }



render() {
        return (
            <>
                {this.props.isFetching
                    ? <ProgressLoader/>
                    : <Suspense fallback={<ProgressLoader/>}>
                        <UsersCard
                            followApi={this.followApi}
                            unfollowApi={this.unfollowApi}
                            {...this.props}
                            onChangePage={this.onChangePage}
                        />
                    </Suspense>
                }
            </>
        )
    }
}


export default UsersApi