import * as axios from "axios";

const getApi = {
    getUsers: (usersCurrentPage = 1, totalUsersCount = 10) => {

        return axios.get(`https://social-network.samuraijs.com/api/1.0/users/?userId=${usersCurrentPage}&count=${totalUsersCount}`,{
            withCredentials: true
        })
            .then( res => {
                return res.data
            })
    },
     getTodos: (currentPage = 1, totalCount = 10) => {
         return axios.get(`https://jsonplaceholder.typicode.com/todos?userId=${currentPage}&count=${totalCount}`)
             .then( res => {
                 return res.data
             })

     },
     getNav: () => {
         return axios.get(`https://social-network.samuraijs.com/api/1.0/auth/me`, {withCredentials:true})
     },
     getProfile :(userId) => {
         return  axios.get(`https://social-network.samuraijs.com/api/1.0/profile/${userId}`)
             .then( res => {

                 return res.data
             })
     },
    getUnfollow: (userId) => {
        return axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${userId}`, {
            withCredentials: true,
            headers: {
                'API-KEY': 'a7d2551e-da40-4551-b901-8b3b94ff143a'
            }
        })
    },
    getFollow: (userId) => {
        return axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${userId}`, {}, {
            withCredentials: true,
            headers: {
                'API-KEY': 'a7d2551e-da40-4551-b901-8b3b94ff143a'
            }
        })
    },
    getFollowTodo: (todoId) => {
        return axios.post(`https://jsonplaceholder.typicode.com/todos/?userId=${todoId}`, {}, {
            withCredentials: true
        })
    },
    getUnfollowTodo: (todoId) => {
        return  axios.delete(`https://jsonplaceholder.typicode.com/todos/?userId=${todoId}`, {
            withCredentials: true
        })
    },
    getStatus: (userId) => {
        return axios.get(`https://social-network.samuraijs.com/api/1.0/profile/status/${userId}`, {
            withCredentials: true
        })
    },
    updateStatus: (status) => {
        return axios.put("https://social-network.samuraijs.com/api/1.0/profile/status", {status: status}, {
            withCredentials: true,
            'API-KEY': 'a7d2551e-da40-4551-b901-8b3b94ff143a'
        })
    },
    login: (email, password, rememberMe = false) => {
        return axios.post("https://social-network.samuraijs.com/api/1.0/auth/login", {
            email,
            password,
            rememberMe
        })
    },
    logout: () => {
        return axios.delete("https://social-network.samuraijs.com/api/1.0/auth/login")
    }

}

export default getApi






