import {Route, Switch} from 'react-router-dom'
import HomeContainer from "./containers/HomeContainer";
import LoginReduxConnect from "./pages/Login";
import Register from "./pages/Register";
import PostContainer from "./containers/PostContainer";
import ProfileContainer from "./containers/ProfileContainer";
import TodosContainer from "./containers/TodosContainer";
import {Component} from "react";
import {getThunkAuth, getThunkLogout} from "./redux/auth_reducer";
import NavApi from "./api_components/NavApi";
import {ProgressLoader} from "./components/Loader/ProgressLoader";
import {appLoadingThunk} from "./redux/app_reducer";
import {compose} from "redux";
import {withRouter} from "react-router";
import {connect} from "react-redux";
import {getEmail, getId, getIsAppLoading, getIsAuth, getIsFetching, getLogin} from "./selectors/app_selectors";
import UsersContainer from "./containers/UsersContainer";

// const UsersContainer = lazy(() => import('./containers/UsersContainer'))


class App extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.getThunkAuth()
        this.props.appLoadingThunk()
    }

    render() {
        return (
            <div>
                {
                    this.props.isAppLoading === true
                        ? <div>
                            <NavApi {...this.props}/>
                            <Switch>
                                <Route path="/posts">
                                    <PostContainer/>
                                </Route>
                                <Route path="/users">
                                    <UsersContainer/>
                                </Route>
                                <Route path="/todos">
                                    <TodosContainer/>
                                </Route>
                                <Route path="/profile/:id?">
                                    <ProfileContainer/>
                                </Route>
                                <Route path="/login">
                                    <LoginReduxConnect/>
                                </Route>
                                <Route path="/register">
                                    <Register/>
                                </Route>
                                <Route path="/">
                                    <HomeContainer/>
                                </Route>
                            </Switch>
                        </div>
                        : <ProgressLoader/>
                }
            </div>
        )

    }
}

const mapStateToProps = (state) => {
    return {
        id: getId(state),
        email: getEmail(state),
        login: getLogin(state),
        isFetching: getIsFetching(state),
        isAuth: getIsAuth(state),
        isAppLoading: getIsAppLoading(state)
    }
}


export default compose(
    withRouter,
    connect(mapStateToProps,
        {
            getThunkAuth,
            getThunkLogout,
            appLoadingThunk
        }
    )
)(App)

