import {connect} from "react-redux";
import {
    getTodosFollowCreator,
    getTodosThunkCreator,
    getTodosUnfollowCreator,
    isTodoLoadingAction,
    setPageAction,
    setTodosAction,
    setTotalCountAction
} from "../redux/todos_reducer";
import TodosApi from "../api_components/TodosApi";

const mapStateToProps = (state) => {

    return {
        todosData: state.todosData.todosData,
        pageSize: state.todosData.pageSize,
        totalCount: state.todosData.totalCount,
        currentPage: state.todosData.currentPage,
        isFetching: state.todosData.isFetching,
        isTodoButtonProgressing: state.todosData.isTodoButtonProgressing
    }
}

const TodosContainer = connect(mapStateToProps, {
    setTodos: setTodosAction,
    setPage: setPageAction,
    setTotalTodosCount: setTotalCountAction,
    setLoading: isTodoLoadingAction,
    getTodosThunkCreator,
    getTodosFollowCreator,
    getTodosUnfollowCreator
})(TodosApi)

export default TodosContainer