import getApi from "../DAL_api/api";

const FOLLOW = 'users/FOLLOW'
const UNFOLLOW = 'users/UNFOLLOW'
const SET_USERS = 'users/SET_USERS'
const SET_CURRENT_USERS_PAGE = 'users/SET_CURRENT_USERS_PAGE'
const TOTAL_USERS_COUNT = 'users/TOTAL_USERS_COUNT'
const IS_LOADING = 'users/IS_LOADING'
const IS_BUTTON_PROGRESSING = 'users/IS_BUTTON_PROGRESSING'

let initialState = {
    usersData: [],
    totalUsersCount: 10,
    usersPageSize: 5,
    usersCurrentPage: 1,
    isFetching: true,
    isButtonProgressing: []
}


export const usersReducer = (state = initialState, action) => {

    switch (action.type) {
        case FOLLOW:

            return {
                ...state,
                usersData: state.usersData.map(user => {
                    if (user.id === action.userId) {
                        return {...user, completed: false}
                    }
                    return user
                })
            }

        case UNFOLLOW:

            return {
                ...state,
                usersData: state.usersData.map(user => {
                    if (user.id === action.userId) {
                        return {...user, completed: true}
                    }
                    return user
                })
            }
        case SET_USERS:

            return {...state, usersData: [...state.usersData, ...action.users]}

        case SET_CURRENT_USERS_PAGE:

            return {...state, usersCurrentPage: action.currentUsersPage}

        case TOTAL_USERS_COUNT:
            return {...state, totalUsersCount: action.totalCount}

        case IS_LOADING:
            return {...state, isFetching: action.loading}

        case IS_BUTTON_PROGRESSING:

            return {
                ...state,
                isButtonProgressing: action.progressing
                    ? [...state.isButtonProgressing, action.userId]
                    : state.isButtonProgressing.filter(id => id !== action.userId)
            }
        default:
            return state
    }
}


export const followAction = (userId) => ({type: FOLLOW, userId})
export const unFollowAction = (userId) => ({type: UNFOLLOW, userId})
export const setUsersAction = (users) => ({type: SET_USERS, users})
export const setUsersPageAction = (currentUsersPage) => ({type: SET_CURRENT_USERS_PAGE, currentUsersPage})
export const setTotalUsersCountAction = (totalCount) => ({type: TOTAL_USERS_COUNT, count: totalCount})
export const isLoadingAction = (loading) => ({type: IS_LOADING, loading})
export const isToggleButtonAction = (progressing, userId) => ({type: IS_BUTTON_PROGRESSING, progressing, userId})

export const getUsersThunkCreator = (usersCurrentPage, totalUsersCount) =>
    async (dispatch) => {

        dispatch(isLoadingAction(true))
        dispatch(setUsersPageAction(usersCurrentPage))
        //axios
        let data = await getApi.getUsers(usersCurrentPage, totalUsersCount)
        dispatch(isLoadingAction(false))
        dispatch(setUsersAction(data.items))
        dispatch(setTotalUsersCountAction(data.totalCount))
    }

export const unfollowThunkCreator = (userId) => async (dispatch) => {
    dispatch(isToggleButtonAction(true, userId))

    let apiMethod = getApi.getUnfollow.bind(getApi)

    let res = await apiMethod(userId)
    if (res.data.resultCode === 0 || 1) {
        dispatch(unFollowAction(userId))
    }

    dispatch(isToggleButtonAction(false, userId))
}

export const followThunkCreator = (userId) => async (dispatch) => {
    dispatch(isToggleButtonAction(true, userId))

    let apiMethod = getApi.getFollow.bind(getApi)

    let res = await apiMethod(userId)
    if (res.data.resultCode === 0) {
        followAction(userId)
    }

    dispatch(isToggleButtonAction(true, userId))
}
