import React from 'react'
import {Button, Typography} from "@material-ui/core";
import {reduxForm} from "redux-form";


 const LogoutSelect = (props) => {

    return (
        <div style={{display: "flex", alignItems: "center"}}>
            <Typography color={"secondary"} variant={'h4'}>{props.login}</Typography>
            &nbsp;
            &nbsp;
            <h2>{props.email}</h2>
            &nbsp;
            <form onSubmit={props.handleSubmit}>
                <Button variant={"outlined"} color={"secondary"} type={"submit"}>Logout</Button>
            </form>
        </div>
    )
}

const LogoutReduxForm = reduxForm(
    {form: 'logout'}
)(LogoutSelect)

export default LogoutReduxForm